import { _decorator } from 'cc';
const { ccclass, property } = _decorator;
/**
 * 投射物属性
 */
@ccclass('ProjectileProperty')
export class ProjectileProperty {

    /**
     * 穿透
     */
    @property
    penetration: number = 1;

    /**
     * 时长
     */
    @property
    liftTime: number = 3.0;

    /**
     * 追踪
     */
    @property
    chase: boolean = false;
}

